import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class SupportService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  searchStudent<T, TE>(data: TE) {
    return this.post<T>("SearchUser/Index", data);
  }

  CowpayReferenceSearch(cowPayId: string) {
    return this.get("CowPay/VerifyCowPayPayment/" + cowPayId);
  }
  resetPassword<T, TE>(data: T) {
    return this.post<TE>("ResetPassword/resetUserPassword", data);
  }

  //##########################################################################
  getCase<T>(userId: number, caseId: number) {
    return this.get<T>(`CaseScreen/returnCase/${userId}/${caseId}`);
  }

  getCaseTable<T>(userId: number) {
    return this.get<T>(`CaseScreen/CaseTable/${userId}`);
  }

  AddEditCase<T>(data: T) {
    return this.post<T>("CaseScreen/AddEditCase", data);
  }
  //##########################################################################

  getRefundBalanceData<T>(userId: number) {
    return this.get<T>("RefundBalance/Refund/" + userId);
  }

  RefundBalance<T>(data: T) {
    return this.post<T>("RefundBalance/Refund", data);
  }

  getStudentBalanceForTeacher(
    userId: number,
    teacherId: number,
    EduCompId: number
  ) {
    return this.get(
      `RefundBalance/returnUserBalanceByTeacherEduCompId/${userId}/${teacherId}/${EduCompId}`
    );
  }

  getStudentDataGenerateCode<T>(userId: number) {
    return this.get<T>(`AJAX/GenerateStudentCode/${userId}`);
  }

  GenerateCode(d: any) {
    return this.post(
      `AJAX/GenerateStudentCode/${d.teacherId}/${d.subjectId}/${d.materialId}/${d.userId}`,
      null
    );
  }

  getSubjects<T>() {
    return this.get<T>("test");
  }

  getTeachersBySubjectId<T>(subjectId: number) {
    return this.get<T>("test/" + subjectId);
  }

  getLectursByTeachersId<T>(teacherId: number) {
    return this.get<T>("test/" + teacherId);
  }
}
