import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class GenerateCodesService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  GenerateCodes(EduCompId: number, data: any) {
    return this.post("codes/GenerateCodes/" + EduCompId, data);
  }

  searchCodes<T>(code: string, counter: string) {
    return this.get<T>("codes/MaterialCodes/" + code + "/" + counter);
  }

  GenerateBalanceCodes<T>(data: T) {
    return this.post<T>("codes/GenerateCustomBalanceCodes", data);
  }

  applyBalanceCode(code: string) {
    return this.post("codes/applyCustomBalanceCode/" + code, null);
  }
}
